@font-face {
    font-family: 'Eulyoo1945-Regular';
    src: url('./Eulyoo_font_201204/Eulyoo1945-Regular.otf') format('otf'),
         url('./Eulyoo_font_201204/TTF/Eulyoo1945-Regular.ttf') format('ttf'),
         url('./Eulyoo_font_201204/webfont/Eulyoo1945-Regular.eot') format('eot'),
         url('./Eulyoo_font_201204/webfont/Eulyoo1945-Regular.woff') format('woff');
}
body {
    font-family: 'Eulyoo1945-Regular';
}
body, div, h1, h2, h3, ul, li, a, img {
    margin: 0;
    padding: 0;
}
h1, h2, h3 {
    font-weight: normal;
    color: rgb(41, 39, 39);
}
a {
    text-decoration: none;
    color: rgb(41, 39, 39);
    padding: 5px;
    border-radius: 3px;
}
li {
    list-style: none;
}
.active {
    background-color: aliceblue;
    text-decoration: none;
}
.menu {
    display: flex;
}
ul#list {
    box-sizing: border-box;
    padding-top: 30px;
    padding-left: 20px;
    line-height: 1.7;
}
.side {
    display: flex;
    flex-direction: column;
    width: 180px;
    flex-shrink: 0;
    align-self: stretch;
    border-right: 1px solid #ccc;
    min-height: 800px;
}
.side > h1 {
    border-bottom: 1px solid #ccc;
    background-color: aliceblue;
    text-align: center;
    padding: 20px 0 22.7px;
}
.main1 {
    margin: 1.3px 30px;
    width: 1200px;
}
.main1 h2 {
    position: relative;
    top: 25px;
}
.main {
    margin: 29.6px 30px;
    width: 1200px;
}
.content {
    display: flex;
    flex-wrap: wrap;
    justify-items: flex-start;
    gap: 0 70px;
}
.content > div {
    margin-bottom: 20px;
}
.main > h2 {
    margin-bottom: 20px;
}
#sp {
    position: relative;
}
#sp > li {
    position: absolute;
}
#sp > li:last-child {
    left: 120px;
    top: 0;
}
.line {
    border-top: 1px solid #ccc;
    margin: 50px -60px 40px -30px;
}
.spe > h2 {
    margin-bottom: 20px;
    padding-top: 15px;
}
h3 {
    margin: 10px;
}
img {
    border-radius: 5px;
}